<template>
    <div class="menu-wrapper">
        <template v-for="item in data">
            <!-- 最后一级菜单 -->
            <el-menu-item
                    v-if="!item.children && !item.hidden"
                    :key="item.path"
                    :index="item.path"
            >
                <i :class="item.meta.icon"></i>
                <span slot="title">{{ item.meta.title }}</span>
            </el-menu-item>

            <!-- 此菜单下还有子菜单 -->
            <el-submenu
                    v-if="item.children && !item.hidden"
                    :key="item.path"
                    :index="item.path"
            >
                <template slot="title">
                    <i :class="item.meta.icon"></i>
                    <span> {{ item.meta.title }}</span>
                </template>
                <!-- 递归 -->
                <sidebar-item
                        :menu="item.children"
                />
            </el-submenu>
        </template>
    </div>
</template>

<script>
    export default {
        name: "menuItems",
        props: ["data"],
        data() {
            return {};
        }
    }
</script>

